*{
  margin: 0px;
  padding: 0px
}

.whole-container {
  display: flex;
  height: 100vh;
  overflow: hidden;
  animation: appear 0.5s ease-in;
}

.whole-container > .lside {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.loading-block {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
}

.disappears {
  animation: disappear 0.5s ease-in;
}

@keyframes appear {
  from {
      opacity: 0%;
  }

  to {
      opacity: 100%;
  }
}

@keyframes disappear {
  from {
    opacity: 100%;
  }

  to {
    opacity: 0%;
  }
}